<template>
  <div class="shell">
    <div class="top">
      <!-- <h2>Welcome Smile's Music</h2> -->
    </div>
    <form onsubmit="return false">
      <div class="form-item">
        <label>用户名</label>
        <div class="input-wrapper">
          <input type="text" id="username" v-model="loginParams.username" autocomplete="off" autocorrect="off" autocapitalize="off" spellcheck="false"
            data-lpignore="true" />
        </div>
      </div>
      <div class="form-item">
        <label>密码</label>
        <div class="input-wrapper">
          <input type="password" v-model="loginParams.password" id="password" autocomplete="off" autocorrect="off" autocapitalize="off"
            spellcheck="false" data-lpignore="true" />
          <button type="button" id="eyeball">
            <div class="eye"></div>
          </button>
          <div id="beam"></div>
        </div>
      </div> 
      <div class="bottom" style="display:flex;justify-content:center;">
        <button id="submit" @click="login">登录</button>
      </div>
      <h6 style="text-align:center;">测试账号密码：smile 123456</h6>
      <h5 style="text-align:center;margin-top:20px;">没有账号？<span @click="$router.back()">前往注册</span></h5>
    </form>
  </div>
</template>

<script>
export default {
  data(){
    return({
      loginParams:{
      username:'',
      password:''
    }
    })
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      const root = document.documentElement;
      const eye = document.getElementById('eyeball');
      const beam = document.getElementById('beam');
      const passwordInput = document.getElementById('password');
      const footer = document.querySelector('.shell-main-footer')
      const shell = document.querySelector('.shell')
      let flag = false

      root.addEventListener('mousemove', (e) => {
        let rect = beam.getBoundingClientRect();
        let mouseX = rect.right + (rect.width / 2);
        let mouseY = rect.top + (rect.height / 2);
        let rad = Math.atan2(mouseX - e.pageX, mouseY - e.pageY);
        let degrees = (rad * (20 / Math.PI) * -1) - 350;
        root.style.setProperty('--beamDegrees', `${degrees}deg`);
      });

      eye.addEventListener('click', e => {
        e.preventDefault();
        flag = !flag
        if (flag) {
          shell.style.backgroundColor = 'black'
        } else {
          shell.style.backgroundColor = 'white'
        }
        document.body.classList.toggle('show-password');
        passwordInput.type = passwordInput.type === 'password' ? 'text' : 'password'
        passwordInput.focus();

      });
    },
    login(){
      if(this.loginParams.username=='' || this.loginParams.password=='' || this.loginParams.username==null || this.loginParams.password==null){
        this.$toast.fail('请输入账号或密码!');
        return
      }else if(this.loginParams.username!='smile'||this.loginParams.password!='123456'){
        this.$toast.fail('账号或密码错误!');
        this.loginParams.username = ''
        this.loginParams.password = ''
        return
      }else{
        this.$toast.success('登录成功!');
        localStorage.setItem('userInfo',this.loginParams.username)
        this.$router.push('/admin')
      }
    }
  }
}
</script>

 <style scoped>
 @import url(./css/all.css);
 .top{
  margin:50px;
 }
 body .shell-main-nav ul .nav-box {
   left: 480px;
 }
 
 :root {
   --bgColor: white;
   --inputColor: black;
   --outlineColor: rgb(141, 228, 41);
 }
 
 * {
   box-sizing: border-box;
 }
 
 body {
   background: var(--bgColor);
 }
 
 body.show-password {
   --bgColor: rgba(0, 0, 0, 0.9);
   --inputColor: white;
   --outlineColor: rgb(151, 247, 218);
 }
 
 form {
   transform: translate3d(0, 0, 0);
   padding: 10px;
 }
 
 form>*+* {
   margin-top: 10px;
 }
 
 .form-item>*+* {
   margin-top: 0.5rem;
 }
 
 label,
 input,
 button {
   font-size: 1.5rem;
   font-family: monospace;
   color: var(--inputColor);
 }
 
 label:focus,
 input:focus,
 button:focus {
   outline: 3px solid var(--outlineColor);
   outline-offset: 2px;
 }
 
 label::-moz-focus-inner,
 input::-moz-focus-inner,
 button::-moz-focus-inner {
   border: none;
 }
 
 label[id=password],
 input[id=password],
 button[id=password] {
   color: black;
 }
 
 button {
   border: none;
 }
 
 [id=submit] {
   cursor: pointer;
   margin: 20px 0 0 2px;
   padding: 0.75rem 1.25rem;
   color: var(--bgColor);
   background-color: var(--inputColor);
   box-shadow: 4px 4px 0 rgba(30, 144, 255, 0.2);
 }
 
 [id=submit]:active {
   transform: translateY(1px);
 }
 
 .input-wrapper {
   position: relative;
 }
 
 input {
   padding: 0.75rem 4rem 0.75rem 0.75rem;
   width: 100%;
   border: 2px solid transparent;
   border-radius: 0;
   background-color: transparent;
   box-shadow: inset 0 0 0 2px black, inset 6px 6px 0 rgba(30, 144, 255, 0.2), 3px 3px 0 rgba(30, 144, 255, 0.2);
   -webkit-appearance: none;
 }
 
 input:focus {
   outline-offset: 1px;
 }
 
 .show-password input {
   box-shadow: inset 0 0 0 2px black;
   border: 2px dashed white;
 }
 
 .show-password input:focus {
   outline: none;
   border-color: rgb(151, 247, 218);
 }
 
 [id=eyeball] {
   --size: 1.25rem;
   display: flex;
   align-items: center;
   justify-content: center;
   cursor: pointer;
   outline: none;
   position: absolute;
   top: 50%;
   right: 0.75rem;
   border: none;
   background-color: transparent;
   transform: translateY(-50%);
 }
 
 [id=eyeball]:active {
   transform: translateY(calc(-50% + 1px));
 }
 
 .eye {
   width: var(--size);
   height: var(--size);
   border: 2px solid black;
   border-radius: calc(var(--size) / 1.5) 0;
   transform: rotate(45deg);
 }
 
 .eye:before,
 .eye:after {
   content: "";
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   margin: auto;
   border-radius: 100%;
 }
 
 .eye:before {
   width: 35%;
   height: 35%;
   background-color: black;
 }
 
 .eye:after {
   width: 65%;
   height: 65%;
   border: 2px solid black;
   border-radius: 100%;
 }
 
 [id=beam] {
   position: absolute;
   top: 50%;
   right: 1.75rem;
   -webkit-clip-path: polygon(100% 50%, 100% 50%, 0 0, 0 100%);
   clip-path: polygon(100% 50%, 100% 50%, 0 0, 0 100%);
   width: 100vw;
   height: 25vw;
   z-index: 1;
   mix-blend-mode: multiply;
   transition: transform 200ms ease-out;
   transform-origin: 100% 50%;
   transform: translateY(-50%) rotate(var(--beamDegrees, 0));
   pointer-events: none;
 }
 
 .show-password [id=beam] {
   background: rgb(151, 247, 218);
 }
 
 body .shell-main-footer {
   position: absolute;
   bottom: 100px;
 }
 
 body .shell {
   height: 100vh;
   position: relative
 }
 
 body .shell-main-footer {
   height: 70px;
 }
 </style>